        console.error("Failed to create course:", error)
        alert("‰øùÂ≠òËØæÁ®ãÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊéßÂà∂Âè∞Êó•Âøó„ÄÇ")
